import React, { useReducer, useMemo, useEffect } from "react";
import { BrowserRouter as Router, Route, Switch, Link } from "react-router-dom";
import ThemeContext from "./ThemeContext";
import { getSettings } from "./services/UserServices";
import { Container } from "react-bootstrap";
// pages

import Home from "./pages/Home";
import AddAccount from "./pages/AddAccount";
import Subscription from "./pages/Subscription";
import Blog from "./pages/Blog";
import Admin from "./pages/Admin";
import AdminLogin from "./pages/AdminLogin";
import SingleBlog from "./pages/SingleBlog";

const themeReducer = (state, action) => {
  switch (action.type) {
    case "change":
      window.localStorage.setItem("theme", action.theme);
      return { ...state, themeName: action.theme };
    default:
      return state;
  }
};
let savedTheme = window.localStorage.getItem("theme");
let initialTheme = savedTheme
  ? { themeName: savedTheme }
  : { themeName: "light" };

function App() {
  const [themeState, themeDispatch] = useReducer(themeReducer, initialTheme);
  const value = useMemo(() => {
    return [themeState, themeDispatch];
  }, [themeState, themeDispatch]);

  useEffect(() => {
    getSettings()
      .then(res => {
        document.title = res.site_name;
      })
      .catch(err => console.log(err));
  }, []);
  const themeButton = () => {
    switch (themeState.themeName) {
      case "light":
        return (
          <button
            type="button"
            onClick={() => themeDispatch({ type: "change", theme: "dark" })}
            className="themeButton light"
          >
            Dark
          </button>
        );
      case "dark":
        return (
          <button
            type="button"
            onClick={() => themeDispatch({ type: "change", theme: "light" })}
            className="themeButton dark"
          >
            Light
          </button>
        );
      default:
        return null;
    }
  };
  const Footer = ({ page }) => {
    const handleClick = () => window.scrollTo({ top: 0 });
    return (
      <section className="pages-controls d-md-none">
        <Container>
          <Link
            to="/add-account"
            onClick={handleClick}
            className={page === "add-account" ? "active" : ""}
          >
            إضافة حساب{" "}
          </Link>
          <Link
            to="/"
            onClick={handleClick}
            className={page === "home" ? "active hover" : "hover"}
          >
            الرئيسية{" "}
          </Link>
          <Link
            to="/subscription"
            onClick={handleClick}
            className={page === "subs" ? "active premium" : "premium"}
          >
            إشتراك
          </Link>
        </Container>
      </section>
    );
  };
  return (
    <div
      className={
        themeState.themeName === "dark" ? "app-container dark" : "app-container"
      }
    >
      <ThemeContext.Provider value={value}>
        <Router>
          <Switch>
            <Route exact path={"/"}>
              {themeButton()}
              <Home />
              <Footer page="home" />
            </Route>
            <Route exact path={"/add-account"}>
              <AddAccount />
              <Footer page="add-account" />
            </Route>
            <Route exact path={"/subscription"}>
              <Subscription />
              <Footer page="subs" />
            </Route>
            <Route exact path={"/blog"}>
              <Blog />
            </Route>
            <Route exact path={"/blog/:title"}>
              <SingleBlog />
            </Route>

            <Route exact path="/admin" component={Admin} />
            <Route exact path="/admin-login" component={AdminLogin} />
          </Switch>
        </Router>
      </ThemeContext.Provider>
    </div>
  );
}

export default App;
